import React, { Children, Component } from "react";
import AccordionItem from "./AccordionItem";
//import { toJS } from "mobx";
import { H2 } from "./general/style/style";

export default class Accordion extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    //console.log("Accordion props: ", toJS(this.props));
    return (
      <>
        { this.props.sectionTitle &&<H2 padding="10px 16px" id={this.props.sectionId}>{this.props.sectionTitle}</H2>}
        {this.props.itemLabels &&
          this.props.itemLabels.map((key, index) => (
            <AccordionItem
              title={key}
              index={index}
              currentAccOpen={this.props.currentAccOpen}
              changeAccIndex={this.props.changeAccIndex}
              className={this.props.className}
              margin={this.props.margin}
              style={this.props.style}
              accordainWrpClassName={this.props.accordainWrpClassName}
            >
              {this.props.children[index]}
            </AccordionItem>
          ))}
      </>
    );
  }
}


